import base from '../../styles-library/base';

const LandingStyles = {
  color: '#545454',
  backgroundColor: `#e4e4e4`,
  overflow: 'auto',
  display: 'inline-block',
  main: {
    fontSize: '1.3rem',
    fontWeight: '200',
    width: '40%',
    backgroundColor: 'white',
    paddingLeft: '36px',
    paddingTop: '9px',
    paddingBottom: '9px',
    marginTop: '2.5rem'
  },
  message: {
  },
  share: {
    textAlign: 'center',
    width: '100%',
    maxWidth: '960px',
    color: 'white',
    cursor: 'pointer',
    paddingBottom: '2.5rem',
    marginTop: '15px',
    button: {
      marginLeft: 'auto',
      marginRight: '16px',
      height: '30px',
      paddingTop: '8px',
      borderRadius: '4px',
      display: 'inline-block',
      textAlign: 'left'
    },
    icon: {
      width: '16px',
      height: '16px',
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'cover',
      display: 'inline-block',
      marginLeft: '12px',
      marginTop: '2px',
      float: 'left'
    },
    text: {
      display: 'inline',
      marginLeft: '10px',
      marginRight: '7px',
      fontSize: '.93rem',
      fontWeight: '300'
    },
    count: {
      float: 'right',
      marginRight: '9px'
    }
  },
  social: {
    twitter: {
      color: {
        backgroundColor: '#1b95e0'
      },
      logo: {
        backgroundImage: `url('facebook-logo.svg')`,
      }
    },
    facebook: {
      color: {
        backgroundColor: '#4267b2'
      },
      logo: {
        backgroundImage: `url('facebook-logo.svg')`,
      }
    }
  }
};


export default Object.assign(LandingStyles, base);



